.btn {
  position: relative;
  padding: 10px 25px;
  margin: 0 auto;
  margin-top: 30px;
  max-width: 250px;
  width: 100%;
  border-radius: 27.5px;
  color: #fff;
  cursor: pointer;
  border: none;
  transition: box-shadow .1s,transform .1s,-webkit-box-shadow .1s,-webkit-transform .1s;
  background: #fad485;
  -webkit-box-shadow: 0 3px 0 0 #ebb16f, 0 4px 0 0 #d99a59, 0 4px 8px 0 rgba(102, 55, 0, 0.4);
          box-shadow: 0 3px 0 0 #ebb16f, 0 4px 0 0 #d99a59, 0 4px 8px 0 rgba(102, 55, 0, 0.4);
}

.btn.small {
  max-width: 180px;
  padding: 8px 20px;
  margin-top: 0;
  font-size: 15px;
  -webkit-box-shadow: 0 3px 0 0 #dedede, 0 4px 0 0 #bfbfbf, 0 4px 8px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 3px 0 0 #dedede, 0 4px 0 0 #bfbfbf, 0 4px 8px 0 rgba(0, 0, 0, 0.3);
}

.btn.small .gradient {
  background: -webkit-gradient(linear, left top, left bottom, from(#f8f6f5), to(#fdfcfb));
  background: -webkit-linear-gradient(#f8f6f5, #fdfcfb);
  background: -o-linear-gradient(#f8f6f5, #fdfcfb);
  background: linear-gradient(#f8f6f5, #fdfcfb);
}

.btn.small:hover span {
  color: #f68b1f;
}

.btn.small .gradient:hover {
  opacity: 1;
}

.btn.small span:hover + .gradient {
  opacity: 1;
}

.btn.small span:hover {
  color: #f68b1f;
}

.btn span {
  position: relative;
  z-index: 5;
  color: #000000;
}

.btn .gradient {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 27.5px;
  -webkit-transition: opacity .15s;
  -o-transition: opacity .15s;
  transition: opacity .15s;
  background: -webkit-gradient(linear, left top, left bottom, from(#f6c164), color-stop(60%, #f6c164), to(#fdcf70));
  background: -webkit-linear-gradient(#f6c164, #f6c164 60%, #fdcf70);
  background: -o-linear-gradient(#f6c164, #f6c164 60%, #fdcf70);
  background: linear-gradient(#f6c164, #f6c164 60%, #fdcf70);
}

.btn .gradient:hover {
  opacity: 0;
}

.btn span:hover + .gradient {
  opacity: 0;
}

.btn-popup__link {
  display: inline-block;
  margin-top: 30px;
}

